.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.background {
  background: cover;
  background-size: 100%;
  opacity: 0.3;
  background-position: center center;
  background: repeat;
  z-index: -100;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
.graph {
  position: relative;
  border-radius: 5px;
  height: 400px;
  width: 800px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.graph-m {
  position: relative;
  border-radius: 5px;
  height: 400px;
  width: 800px;
  margin-top: 20px;
}
.graph-s {
  position: relative;
  border-radius: 5px;
 z-index: 2;
  margin-top: 40px;
  margin-bottom: 40px;
}
.title {
  font-size: 50px;
  color: rgb(255, 255, 255);
  text-shadow: 1px 1px 5px rgb(43, 43, 43);
  width: 100%;
  text-align: center;
}
.title-m {
  font-size: 35px;
  color: rgb(255, 255, 255);
  text-shadow: 1px 1px 5px rgb(43, 43, 43);
  width: 100%;
  text-align: center;
}
.title-s {
  font-size: 20px;
  color: rgb(255, 255, 255);
  text-shadow: 1px 1px 5px rgb(43, 43, 43);
  width: 100%;
  text-align: center;
}
.graphbg {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.9;
  background-color: #fff;
  border-radius: 5px;
  height: 400px;
  width: 800px;
  margin-top: 20px;
  margin-bottom: 20px;
  z-index: -1;
}
.graphbg-m {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.9;
  background-color: #fff;
  border-radius: 5px;
  height: 400px;
  width: 800px;
  margin-top: 20px;
  margin-bottom: 20px;
  z-index: -1;
}
.graphbg-s {

  top: 0;
  left: 0;
  opacity: 0.9;
  background-color: white;
  border-radius: 5px;
  height: 270px;
  width: 320px;
  z-index: 0;
  
}
.subtitle-s{
  padding: 4px;
font-size: 20px;
margin: 2px;
z-index: 4;
}
.msg {
  text-align: center;
}
.App-M {
  display: none;
}
.App-S{
  display: none;
}
@media (max-width: 1024px) {
  .App {
    display: none;
  }
  .App-S {
    display: none;
  }
  .App-M {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
@media (max-width: 850px) {
  .App {
    display: none;
  }
  .App-M {
    display: none;
  }
  .App-S{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
